@import "../../styles/utils/variables.scss";

.button {
  &_size {
    &_small {
      width: 32px;
      height: 32px;
    }

    &_medium {
      width: 40px;
      height: 40px;
    }

    &_large {
      width: 52px;
      height: 52px;
    }
  }

  &_icon_wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
    background: $surface-2;
    border: 1px solid transparent;

    &:hover {
      background: $icons;
    }

    &:active {
      background: #0f1121;
      border: 1px solid $elements;
    }
  }

  &_img {
    width: 16px;
    height: 16px;
  }
}

